<template>
  <div class="about page-container">
    <div v-for="(v, i) in ABOUT_CONTENTS" :key="i" class="info-paragraph mt-0 mb-90">
      <h2 class="mt-0 mb-36 fs-36">{{ v.title }}</h2>
      <template v-for="(item, index) in v.paragraphs">
        <img :key="index + 'i'" v-if="item.type && item.type == 'img'" class="info-img" :class="item.class" :src="getImg(item.url)" />
        <p class="mt-0 mb-36 text-wrap fw-3 fs-20" :key="index + 'p'" v-html="item" v-else></p>
      </template>
    </div>
  </div>
</template>
    
    <script>
import { ABOUT_CONTENTS } from "@/constant";
export default {
  name: "AboutPage",
  components: {},
  data() {
    return {
      ABOUT_CONTENTS,
    };
  },
  computed: {
    getImg() {
      return function (url) {
        return require(`@/assets/${url}`);
      };
    },
  },
  methods: {},
};
</script>
    
    <style lang="scss">
@import "../style/about.scss";
</style>
    